* {
  border: 1px solid black;
}

html,body {
	height:100%;
}

#top-image {
	background: url('http://kingofwallpapers.com/keanu-reeves/keanu-reeves-001.jpg') center center no-repeat;
	-webkit-background-size: cover;
 	-moz-background-size: cover;
 	-o-background-size: cover;
 	background-size: cover;
	height: 100%;
	width: 100%;
	display:flex;
	align-items: center;
}

#head-text {
	font-weight:200;
	max-width:60rem;
	margin: 0 auto;
	text-align:center;
	color:white;
	text-shadow: 2px 2px black;
	h1{
		font-weight:bold;
		text-transform:uppercase;
	}
}

.about div img{
	width: 50%;
}

section {
	margin:2rem;
	height: auto;
	padding:2rem;
}

.works {
	background-color: #555555;
	color: #FFFEFE;
	display: inline-block;
	width: 100%;
}

.work-text {
	display: inline-block;
	float: left;
	max-width:55%;
	text-align: left;
}

.work-img {
	display: inline-block;
	float: right;
	width: 40%;
	height: 15rem;
}

.work-header {
	margin-bottom: 20px;
	text-align: center;
}

.more-img {
	display: inline-block;
	height: 15rem;
	margin-top: 8px;
}

.footer {
	background-color: #393939;
	color: #FFF;
	text-align: center;
	margin-bottom: 0;
}

h5, h6 {
	color: #529BFF;
}


.more-text {
	padding-right: 5px;
}

.more-img {
	padding-left: 15px;
	margin-right: -15px;
}

/* Small devices (landscape phones, 544px and up) */
@media (min-width: 544px) {
  h1 {font-size:4.5rem;}
}

/* Medium devices (tablets, 768px and up) The navbar toggle appears at this breakpoint */
@media (min-width: 768px) {
  h1 {font-size:5rem;}
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  h1 {font-size:5.5rem;}
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  h1 {font-size:6rem;}
}